import Vue from 'vue'
import App from './App'
// import '@/plugins/axios'
import router from '@/plugins/router'
import { store } from '@/plugins/store'
import * as Global from '@/plugins/util/global'
import vuetify from './plugins/vuetify'
import { initializeApp } from "firebase/app";
import { getAnalytics } from "firebase/analytics";
import { getAuth,signInAnonymously,onAuthStateChanged } from "firebase/auth";
import {getFirestore} from 'firebase/firestore'

const firebaseConfig = {
  apiKey: "AIzaSyAfm9m3oAkJ7fOUManTGUwRn_LenMPCHWY",
  authDomain: "somesomechecker.firebaseapp.com",
  projectId: "somesomechecker",
  storageBucket: "somesomechecker.appspot.com",
  messagingSenderId: "393207096656",
  appId: "1:393207096656:web:acdbdaf03ec22f5a667855",
  measurementId: "G-MF9LCHS9G0"
};
const firebaseApp = initializeApp(firebaseConfig);
const analytics = getAnalytics(firebaseApp);
const auth = getAuth()
Vue.prototype.$currentUser = null
signInAnonymously(auth).then(() => {
}).catch((e) => {
  console.error(e)
})
onAuthStateChanged(auth, (user) => {
  if (user) {
    console.log(user)
    Vue.prototype.$currentUser = user
  }
});
const db = getFirestore()
Object.keys(Global).forEach(k => {
    Vue.use(Global[k])
})
Vue.prototype.$firebase = firebaseApp
Vue.prototype.$analytics = analytics
Vue.prototype.$auth = auth
Vue.prototype.$db = db
Vue.prototype.$bus = new Vue()
Vue.config.productionTip = false

window.vm = new Vue({
  render: h => h(App),
  vuetify,
  router,
  store
}).$mount('#app')
