import Vue from 'vue'
import VueRouter from 'vue-router'

import Landing from '@/pages/landing'
import NotFound from '@/pages/error/error404'

import Dashboard from '@/pages/dashboard'
Vue.use(VueRouter)

const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes: [{
            path: '/not-found',
            name: 'NotFound',
            component: NotFound,
        },
        {
            path: '',
            name: 'Landing',
            component: Landing
        },
        {
            path: '/dashboard',
            name: 'Dashboard',
            component: Dashboard,
            meta:{bottomNav:0}
        },
        {
            path: '/input-name',
            name: 'InputName',
            component: () => import("@/pages/inputpage/input-name"),
            meta:{bottomNav:0}
        },
        {
            path: '/input-details',
            name: 'InputDetails',
            component: () => import("@/pages/inputpage/input-details"),
            meta:{bottomNav:0}
        },
        {
            path: '/check-some',
            name: 'InputDetails',
            component: () => import("@/pages/check-some"),
            meta:{bottomNav:0}
        },
        {
            path: '/alarm',
            name: 'InputDetails',
            component: () => import("@/pages/alarm-history"),
            meta:{bottomNav:1}
        },
        {
            path: '/settings',
            name: 'Settings',
            component: () => import("@/pages/settings"),
            meta:{bottomNav:2}
        }
    ]
})

export default router
