<template>
  <v-app class="off-drag">
    <v-main>
      <transition
      name="fade"
      mode="out-in"
      @beforeLeave="beforeLeave"
      @enter="enter">
      <router-view :w="appWidth" :h="appHeight-56" style="margin-bottom:56px;"/>
      </transition>
<v-bottom-navigation
    fixed
    grow
    v-model="tab"
    :background-color="color"
    dark
    shift
  >
    <v-btn height="56" :color="color" @click="$router.push('/dashboard')">
      <span>썸썸체크</span>

      <v-icon>mdi-heart</v-icon>
    </v-btn>

    <v-btn height="56" :color="color" @click="$router.push('/alarm')">
      <span>썸썸이력</span>

      <v-icon>mdi-alarm</v-icon>
    </v-btn>

    <v-btn height="56" :color="color" @click="$router.push('/settings')">
      <span>설정</span>

      <v-icon>mdi-cog</v-icon>
    </v-btn>
  </v-bottom-navigation>
      <loading-indicator/>
      <toast/>
    </v-main>

  </v-app>
</template>

<script>
import LoadingIndicator from './components/loading-indicator';
import Toast from './components/toast'

export default {
  name: 'App',

  components: {
    LoadingIndicator,
    Toast,
  },

  data: () => ({
    appWidth: innerWidth,
    appHeight: innerHeight,
    tab:0
  }),
  computed:{
    color () {
        switch (this.tab) {
          case 0: return 'pink'
          case 1: return 'indigo'
          case 2: return 'blue-grey'
          default: return 'blue-grey'
        }
      },
    bottomNav(){
      return this.$route.meta.bottomNav
    }
  },
  watch:{
    bottomNav(newval){
      this.tab = newval
    }
  },
  created() {
    window.addEventListener('resize', () => {
      this.appWidth = window.innerWidth
      this.appHeight = window.innerHeight
    })
  },
  methods: {
    beforeLeave(element) {
      this.prevHeight = getComputedStyle(element).height;
    },
    enter(element) {
      const { height } = getComputedStyle(element);
      element.style.height = this.prevHeight;
      setTimeout(() => {
        element.style.height = height;
      });
    },
  }
};
</script>
<style>

.off-drag{
  -ms-user-select: none;
  -moz-user-select: -moz-none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}



.fade-enter-active,
.fade-leave-active {
  transition-duration: 0.3s;
  transition-property: opacity;
  transition-property: height, opacity;
  transition-timing-function: ease;
  overflow: hidden;
}

.fade-enter,
.fade-leave-active {
  opacity: 0
}
</style>